
///////////////////////////////////////////////////////////////////////////////
// Archivo  paginationView   arquitectura MVC
// Este c√≥digo define  la vista de paginaci√≥n para que la aplicaci√≥n web 
// muestre m√∫ltiples resultados en p√°ginas 
//Mostrar los botones de paginaci√≥n (anterior y siguiente).
// Detectar cu√°ndo el usuario hace clic en un bot√≥n de paginaci√≥n.
//Comunicarle al controlador (controller) qu√© p√°gina se debe cargar.
///////////////////////////////////////////////////////////////////////////////
import View from './View.js';
import icons from 'url:../../img/icons.svg';
class PaginationView extends View {
  _parentElement = document.querySelector('.pagination');
  addHandlerClick(handler) {
    this._parentElement.addEventListener('click', function (e) {
      const btn = e.target.closest('.btn--inline');
      if (!btn) return;
      const goToPage = +btn.dataset.goto;
      handler(goToPage);
    });
  }
  //  M√©todo privado _generateMarkup
  _generateMarkup() {
    // P√°gina actual
    const curPage = this._data.page;

    // Total de p√°ginas
    const numPages = Math.ceil(
      this._data.results.length / this._data.resultsPerPage
    );

    //  Caso a P√°gina 1 y hay m√°s p√°ginas
    if (curPage === 1 && numPages > 1) {
      return `
        <button data-goto="${curPage + 1}" class="btn--inline pagination__btn--next">
          <span>Page ${curPage + 1}</span>
          <svg class="search__icon">
            <use href="${icons}#icon-arrow-right"></use>
          </svg>
        </button>
      `;
    }

    // Casob √öltima p√°gina y hay m√°s de una
    if (curPage === numPages && numPages > 1) {
      return `
        <button data-goto="${curPage - 1}" class="btn--inline pagination__btn--prev">
          <svg class="search__icon">
            <use href="${icons}#icon-arrow-left"></use>
          </svg>
          <span>Page ${curPage - 1}</span>
        </button>
      `;
    }

    // Casoc. Cualquier p√°gina del medio
    if (curPage < numPages) {
      return `
        <button data-goto="${curPage - 1}" class="btn--inline pagination__btn--prev">
          <svg class="search__icon">
            <use href="${icons}#icon-arrow-left"></use>
          </svg>
          <span>Page ${curPage - 1}</span>
        </button>

        <button data-goto="${curPage + 1}" class="btn--inline pagination__btn--next">
          <span>Page ${curPage + 1}</span>
          <svg class="search__icon">
            <use href="${icons}#icon-arrow-right"></use>
          </svg>
        </button>
      `;
    }

    // Caso d: Solo una p√°gina
    return '';
  }


}

export default new PaginationView();